export const getStorage = (str) => {
  if (sessionStorage.getItem(str) != '' || sessionStorage.getItem(str) != undefined) {
    return JSON.parse(sessionStorage.getItem(str))
  } else {
    return undefined
  }
}
export const setStorage = (key, str) => {
  let message = JSON.stringify(str)
  sessionStorage.setItem(key, message)
}