import Vue from 'vue'
import VueRouter from 'vue-router'
// import Home from '../views/Home.vue'
import Home from '../components/common/Home.vue'
import Login from '../views/Login'
Vue.use(VueRouter)
    // 关闭路由重复报错警告
const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
    return originalPush.call(this, location).catch(err => err)
}

/*
 * meta  路由元信息  isLogin字段判断该页面是否需要登录  rol字段 登录权限
 *  -title 代表面包屑名称
 * */
const routes = [{
        path: '/login',
        name: 'login',
        component: Login,
        meta: {
            isLogin: false,

        },
    },
    {
        path: '/home',
        name: 'home',
        component: Home,
        children: [{
                path: '/home/notice',
                name: 'notice',
                component: () =>
                    import ('../views/Notice.vue'),
                meta: {
                    title: '公告主页',
                    isLogin: true,
                    role: ['admin', 'student']
                }
            },

            {
                path: '/home/usermsg',
                name: 'usermsg',
                component: () =>
                    import ('../views/Usermsg.vue'),
                meta: {
                    title: '管理员信息管理',
                    isLogin: true,
                    role: ['admin']
                }
            },
            {
                path: '/home/changeNotice',
                name: 'changeNotice',
                component: () =>
                    import ('../views/ChangeNotice.vue'),
                meta: {
                    title: '公告管理',
                    isLogin: true,
                    role: ['admin']
                }
            },
            {
                path: '/home/dormitory',
                name: 'dormitory',
                component: () =>
                    import ('../views/Dormitory.vue'),
                meta: {
                    title: '宿舍管理',
                    isLogin: true,
                    role: ['admin']
                }
            },
            {
                path: '/home/student',
                name: 'student',
                component: () =>
                    import ('../views/Student.vue'),
                meta: {
                    title: '学生管理',
                    isLogin: true,
                    role: ['admin', 'student']
                }
            },
        ],
        redirect: '/home/notice'
    },
    {
        path: '/',
        redirect: '/login'
    }
]

const router = new VueRouter({
    routes
})

export default router