<template>
  <div class="login-wrap" :style="loignStyle.backgroundImage">
    <div class="ms-login">
        <div class="ms-title">{{loignStyle.title}}</div>
        <el-form :model="form" :rules="rules" ref="login" label-width="0px" class="ms-content">
            <el-form-item prop="username">
                <!-- <el-button slot="prepend" icon="el-icon-lx-people"></el-button> -->
                <i class="el-icon-user"></i>
                <el-input v-model="form.username" placeholder="username">
                    <!-- <i class="el-icon-delete"></i> -->
                </el-input>
            </el-form-item>
            <el-form-item prop="password">
                <!-- <el-button slot="prepend" icon="el-icon-lx-lock"></el-button> -->
                <i class="el-icon-lock"></i>
                <el-input type="password" placeholder="password" v-model="form.password" @keyup.enter.native="submitForm()">
                    
                </el-input>
            </el-form-item>
            <el-form-item prop="type">
                <el-radio-group v-model="form.type">
                    <el-radio label="学生"></el-radio>
                    <el-radio label="管理员"></el-radio>
                </el-radio-group>
            </el-form-item>
            <div class="login-btn">
            <el-button type="primary" @click="submitForm()">登录</el-button>
            </div>
            <p class="login-tips">学生账号：101，密码101。</p>
            <p class="login-tips">普通管理员账号：202，密码202。</p>
            <p class="login-tips">超级管理员账号：201，密码201。</p>
        </el-form>
    </div>
  </div>
</template>

<script>
import { loignStyle } from '../utils/utils'
import { setStorage } from '../utils/changeSessionStorage'
export default {
    data: function () {
        return {
        form: {
            username: '',
            password: '',
            type:''//学生还是管理员
        },
        rules: {
            username: [{ required: true, message: '请输入用户名', trigger: 'blur' }],
            password: [{ required: true, message: '请输入密码', trigger: 'blur' }],
            type: [{ required: true, message: '请选择登陆身份', trigger: 'blur' }],
        },
        loignStyle
        };
    },
    methods: {
        submitForm() {
        this.$refs.login.validate(valid => {
            if (valid) {
                // console.log('type',this.form.type);
                this.axios({
                    method: 'get',
                    params:{
                        username:this.form.username,
                        password:this.form.password,
                        type:this.form.type,
                    },
                    url:'/login/getuser'
                }).then(data => {
                    console.log('%c ======>>>>>>>>','color:orange;',data)
                    if (data.data.code == 200) {
                        if (data.data.data.length > 0) {
                            this.$message.success('登录成功');
                            setStorage('userInfo',data.data.data[0])
                            this.$store.dispatch('changeAddUser',data.data.data[0])
                            // console.log('store',this.$store.state);
                            this.$router.push('/home');
                            if (data.data.data[0].dormType) {
                                if (data.data.data[0].dormType == 2) {
                                    this.$alert('你的寝室水电费余额不足，请尽快缴费', '提示', {
                                        confirmButtonText: '确定',
                                    });
                                }
                            }
                        }else{
                            this.$message({
                                message: '账号或密码错误',
                                type: 'error'
                            });
                        }
                        
                        // _this.getcurrentData()
                    } else {
                        this.$message({
                            message: data.data.msg,
                            type: 'error'
                        });
                    }
                })
                
            } else {
                // this.$message.error('请输入账号和密码');
                console.log('error submit!!');
                return false;
            }
        });
        },
    },
};
</script>

<style scoped>
    .el-radio-group{
        margin: auto;
    }
    .el-form-item--small /deep/ .el-form-item__error{
        margin-left: 40px;
    }
    .el-form-item /deep/ .el-form-item__content{
        display: flex;
        align-items: center;
    }
    .el-icon-user{
        margin-right: 20px;
    }
    .el-icon-lock{
        margin-right: 20px;
    }
    .login-wrap {
        position: relative;
        width: 100%;
        height: 100%;
        /* background-image: url(../assets/img/login-bg.jpg); */
        background-size: 100%;
    }
    .ms-title {
        width: 100%;
        line-height: 50px;
        text-align: center;
        font-size: 20px;
        color: #4994fb;
        border-bottom: 1px solid #ddd;
    }
    .ms-login {
        position: absolute;
        left: 50%;
        top: 50%;
        width: 350px;
        margin: -190px 0 0 -175px;
        border-radius: 5px;
        background: rgba(255, 255, 255, 0.3);
        overflow: hidden;
    }
    .ms-content {
        padding: 30px 30px;
    }
    .login-btn {
        text-align: center;
    }
    .login-btn button {
        width: 100%;
        height: 36px;
        margin-bottom: 10px;
    }
    .login-tips {
        font-size: 12px;
        line-height: 30px;
        color: #fff;
    }
</style>