import {
  mapState
} from 'vuex'
export default {
    data() {
        return {
            tableData: [],/* 页面的数据 */
            // currentData:[],/* 当前页数据 */
            currentPage: 1,/* 当前页 */
            pageNum: 5, /* 每页显示数据条数 */
            addState: false,/* 添加状态 --公用 */
            updateState: false,/* 修改状态 --公用 */
            formAdd: {},/* 添加的数据 --公用 */
            formUpdate: {},/* 修改的数据 --公用 */
            formSelect: {},/* 查询的数据集 --公用 */
        }
    },
    computed: {
        ...mapState(['user']),
        // /* 当前页数据 */
        currentData() {
            console.log('currentData重新执行',this.tableData);
            let qishi = (this.currentPage - 1) * this.pageNum
            let jieshu = qishi + this.pageNum
            return this.tableData.slice(qishi, jieshu)
            // console.log('currentData',this.currentData);
            // this.$forceUpdate();
        },
        /* 总页码  一共多少页 */
        pageCount() {
            return Math.ceil(this.tableData.length / this.pageNum)
        }
    },
    watch:{
        // tableData: {
        // 　　 handler(newName, oldName) {
        // 　　     console.log('tableData深度监听发生了变化',this.currentData);
        // 　　 },
        // 　　 immediate: true
        // },
    //     currentData: {
    //     　　handler(newName, oldName) {
    // 　　        console.log('currentData深度监听发生了变化',this.currentData);
    //             this.$forceUpdate()
    //     　　},
    //     　　immediate: true
    //     }
    },
    methods: {
        handleCurrentChange(val) {
            console.log('page发生改变');
            this.currentPage = val
            // this.getcurrentData()
        },
        /* 查询table数据 */
        onSelect(obj,callback) {
            /* this.selectUrl:每个页面查询接口地址  在页面主键上定义 */
            /* this.formSelect:每个页面查询接口数据  在页面主键上定义 */
            const _this = this
            let reqData = obj || {
                url: this.selectUrl,
                params: this.formSelect
            }
            this.axios({
                method: 'get',
                ...reqData
            }).then(data => {
                console.log('%c ======>>>>>>>>','color:orange;',data)
                if (data.data.code == 200) {
                    _this.tableData = data.data.data
                    // console.log('tabledata', _this.tableData);
                    callback && callback(data.data.data)
                    // _this.getcurrentData()
                } else {
                    _this.$message('服务器报错');
                }
            })
        },
        /* 修改table数据 */
        update(obj) {
            const _this = this
            this.updateState = false
            this.axios({
                method: 'post',
                data: obj.data,
                url: obj.url
            }).then(data => {
                if (data.data.code == 200) {
                    _this.$message({
                        type: 'success',
                        message: data.data.msg
                    });
                    _this.onSelect({url:obj.updateUrl},obj.callback)
                    // obj.callback && obj.callback()
                    // _this.currentPage = 1
                    
                } else {
                    _this.$message(data.data.msg);
                    console.log(data)
                }
            })
        },
        /* 跟新数据弹框 */
        updateForm(row) {
            this.updateState = true
            this.formUpdate = {
                ...row
            }
        },
        /* 删除table数据 */
        del(obj) {
            return this.axios({
                method: 'post',
                data: obj.data,
                url: obj.url
            })
            .then(data => {
                // console.log(data)
                if (data.data.code == 200) {
                    this.$message({
                        type: 'success',
                        message: data.data.msg
                    });
                    this.onSelect({url:obj.updateUrl},obj.callback)
                    // this.currentPage = 1
                } else {
                    this.$message(data.data.msg);
                }
                this.formAdd = {}
            })
        },
        /* 添加table数据 */
        add(obj) {
            return this.axios({
                method: 'post',
                data: obj.data,
                url: obj.url
            })
            .then(data => {
                // console.log(data)
                if (data.data.code == 200) {
                    this.$message({
                        type: 'success',
                        message: data.data.msg
                    });
                    if (obj.updateUrl) {
                        this.onSelect({url:obj.updateUrl},obj.callback)
                    } else {
                        if (obj.callback) {
                            obj.callback()
                        } 
                    }
                } else {
                    this.$message(data.data.msg);
                }
                this.formAdd = {}
            })
        },
        /* 将时间转化为时间戳 */
        timestamp(time){
            return (new Date("2015/06/23 08:00:20")).getTime()/1000;
        },
        /* 将时间戳转化为正常日期 */
        stamptime(time){
            return new Date(parseInt(time) * 1000).toLocaleString().replace(/:\d{1,2}$/,' ');     
        },
    }
}