import Vue from 'vue';
import App from './App.vue';
import router from './router';
import store from './store'
import ElementUI from 'element-ui';
import VueI18n from 'vue-i18n';
import http from './utils/request'

import {
    messages
} from './components/common/i18n';
import 'element-ui/lib/theme-chalk/index.css'; // 默认主题
// import './assets/css/theme-green/index.css'; // 浅绿色主题
import './assets/css/icon.css';
import './components/common/directives';
import 'babel-polyfill';

Vue.config.productionTip = false;
Vue.prototype.axios = http
Vue.use(VueI18n);
Vue.use(ElementUI, {
    size: 'small'
});
const i18n = new VueI18n({
    locale: 'zh',
    messages
});

//使用钩子函数对路由进行权限跳转
router.beforeEach((to, from, next) => {
    // next()
    console.log('store.state.user', store.state.user);
    // if (to.meta.isLogin && store.state.user.xingMing != undefined || to.fullPath == "/login") {
    // next()
    // } else {
    //     next('/login')
    // }
    if (to.meta.isLogin && store.state.user.id != undefined) {
        //代表需要登录且已经登录
        let loginType = ''
            // if (store.state.user.type) {
            //     //代表登陆者是管理员
            //     loginType = 'admin'
            // } else {
            //     loginType = 'student'
            // }
        store.state.user.type ? loginType = 'admin' : loginType = 'student'
        console.log('loginType', loginType);
        if (to.meta.role.includes(loginType)) {
            console.log('包含');
            next()
        } else {
            console.log('不包含');
            alert('你没有访问该页面的权限')
            next('/login')
        }
    } else if (to.fullPath == "/login") {
        next()
    } else {
        next('/login')
    }
});

new Vue({
    router,
    i18n,
    store,
    render: h => h(App)
}).$mount('#app');