<template>
  <div class="sidebar">
    <div v-if="indexListNav.isLeftNav" style="text-align: center;color:#454545;margin:2rem 0;font-size:16px ">
      <h2>个人信息</h2>
      <h3 style="color:#4985FB;margin:1rem 0;font-size:14px ">欢迎您!{{user.xingMing}}</h3>
      <h3 style="color:#4985FB;margin:1rem 0;font-size:14px ">所属部门 : {{user.buMen}}</h3>
      <h3 style="font-size:14px;margin:1rem 0">角色 : <span style="color:#4985FB">{{user.quanXian}}</span></h3>
      <el-button size="medium" type="primary" @click="loginOut">退 &nbsp; &nbsp; 出</el-button>
    </div>
    <el-menu class="sidebar-el-menu" default-active="/home/zhuye" :collapse="collapse" :background-color="indexListNav.silderbg.backgroundColor" :text-color="indexListNav.silderbg.textColor" :active-text-color="indexListNav.silderbg.activeTextColor" unique-opened router>
      <template v-for="item in items">
        <template v-if="item.subs">
          <el-submenu :index="item.index" :key="item.index">
            <template slot="title">
              <i :class="item.icon"></i>
              <span slot="title">{{ item.title }}</span>
            </template>
            <template v-for="subItem in item.subs">
              <el-submenu v-if="subItem.subs" :index="subItem.index" :key="subItem.index">
                <template slot="title">{{ subItem.title }}</template>
                <el-menu-item v-for="(threeItem,i) in subItem.subs" :key="i" :index="threeItem.index">{{ threeItem.title }}</el-menu-item>
              </el-submenu>
              <el-menu-item v-else :index="subItem.index" :key="subItem.index">{{ subItem.title }}</el-menu-item>
            </template>
          </el-submenu>
        </template>
        <template v-else>
          <el-menu-item :index="item.index" :key="item.index">
            <i :class="item.icon"></i>
            <span slot="title">{{ item.title }}</span>
          </el-menu-item>
        </template>
      </template>
    </el-menu>
  </div>
</template>

<script>
import bus from '../common/bus';
import { indexListNav } from '../../utils/utils'
import { getStorage } from '../../utils/changeSessionStorage'
import tableMixin from '../../mixins/tableMixin'
import { mapActions } from 'vuex'
export default {
  props: ['mystyle'],
  mixins: [tableMixin],
  data() {
    return {
      collapse: false,
      items: [
        {
          icon: 'el-icon-eleme',
          index: '/home/notice',
          title: '公告主页'
        },

        {
          icon: 'el-icon-picture-outline-round',
          index: '/home/dormitory',
          title: '宿舍管理'
        },
        {
          icon: 'el-icon-help',
          index: '/home/student',
          title: '学生管理'
        },
        {
          icon: 'el-icon-copy-document',
          index: '/home/changeNotice',
          title: '公告管理'
        },
        {
          icon: 'el-icon-user',
          index: '/home/usermsg',
          title: '管理员管理'
        },
      ],
      indexListNav,
    };
  },
  computed: {
    onRoutes() {
      return this.$route.path.replace('/', '');
    }
  },
  created() {
    // 通过 Event Bus 进行组件间通信，来折叠侧边栏
    bus.$on('collapse', msg => {
      this.collapse = msg;
      bus.$emit('collapse-content', msg);
    });
    if (!getStorage('userInfo').type) {
      this.items = [
        {
          icon: 'el-icon-eleme',
          index: '/home/notice',
          title: '公告主页'
        },
        {
          icon: 'el-icon-help',
          index: '/home/student',
          title: '学生管理'
        }
      ]
    }
  },
  methods: {
    ...mapActions(['loginOut']),
  }
};
</script>

<style scoped>
.sidebar {
  display: block;
  position: absolute;
  left: 0;
  top: 70px;
  bottom: 0;
  overflow-y: scroll;
}
.sidebar::-webkit-scrollbar {
  width: 0;
}
.sidebar-el-menu:not(.el-menu--collapse) {
  width: 250px;
}
.sidebar > ul {
  height: 100%;
}
</style>
